/* Body */

body::-webkit-scrollbar {
  display: none; /* Hide scrollbar for Chrome, Safari and Opera */
}

body{
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

/* Modal */
.modal-footer{
  justify-content: center;
}

/* NavbarTitle */
.navbarTitle{
  font-size: 90%;
}

.linkColor{
  text-decoration: none;
  color: #0000009e;
}

/* Landing Page */

.landing {
  background-image: url("./component/images/Dubai.jpg");
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 25em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.9);
}


/* Packages */
.sameHeightPackages{
  height: 23em; 
}

.packagesCard{
  display: flex;
  justify-content: around;
}

.fa-check{
  color: #6a00ff;
}

.amount{
  color: #6a00ff;
  font-weight: bold;
  font-size: 2em;
}

.card-img-top{
  height: 50%;
}

.cardHover{
  border: 0;
  transition: transform .4s, box-shadow .4s;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.13);
}

.cardHover:hover{
  transform: scale(1.05);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.27) ;
}

.gradientCard{
  background-image: -webkit-linear-gradient(top, rgba(0,0,0,0.001) 55%, #000 70%, #000 100%);
}

.packagesDetails{
  padding: 15.5rem 0 0 0;
}

.packagesCardItems{
  padding: 0 0 8rem 0;
}


/* Why Us */
.whyusCard {
  border: 0;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.13);
  width: 75%;
}

.whyus{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.whyusBg{
  background-image: linear-gradient(176deg,#ffffff 30%, #202428f8 30%)
}

.whyusCard:hover>div>div>div>#whyusTitle1{
  color: #ff4f5a
}
.whyusCard:hover>div>div>div>#whyusTitle2{
  color: #ffbf9c
}
.whyusCard:hover>div>div>div>#whyusTitle3{
  color: #0097da
}


/* Aboutus */
.aboutUsImg{
  background-image: url("./component/images/Aboutus.jpg");
  background-attachment: fixed;
  background-position: bottom;
  background-size: cover;
  height: 300px;
}

.aboutUsTitle{
  color: #6a00ffe9;
  font-weight: bolder;
  font-size: 8vh;
}

.aboutUsText{
  width: 50%;
  font-weight: 600;
  margin: 10vw;
  padding: 1rem;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .aboutUsText{
    width: 100%;
    margin: 2rem 0 2rem 0;
  }
}




/* Customize Packages Component */

.customizePackagesComponent{
  margin-top: 20vh;
  /* display: grid;
  grid-auto-flow: column;
  grid-template-columns: 1fr 1fr; */
  display: flex;
  flex-wrap: wrap;
  width: null;
}

.customizePackagesInnerComponent1{
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: space-around;
  margin: 2vh;
  /* width: 40%; */
}

.customizePackagesInnerComponent2{
  /* padding-left: 3rem; */
  flex: 1;
  display: flex;
}

.customizeComponentImage{
  width: null;
  flex: 1;
  height: 300;
  /* width: 35%; */
}



/* Testimonials */
.testimonials{
  display: flex;
  flex-wrap: wrap;
}

.testimonialsTitle{
  text-align: center;
  margin: 1rem 0 1rem 0;
}

.testimonialCard{
  width: 30%;
}

.testimonialDp{
  height: 50px;
  width: 50px;
  border-radius: 50%;
}